		"messages": [
			{
				"time": "fall 1903",
				"from": "Germany",
				"body": "Das Reich\n\n[Kuva kuopasta, jossa on ihmisruumiita]\n\nSaksalainen sotakuvaaja Uwe Boll on tuonut tietoomme jrkyttvi kuvia Romaniasta. Venlisen sortohallinnon alla maassa on riehunut suuri nlnht, ja jopa keisarillinen valloittajalaivasto krsi suuresti viime talvena.\n\n[Kuva itkevst mummosta]\n\nIlmeisesti Romaniasta oli trkesti rystetty ruokatarvikkeita ja arvoesineit Venjn pohjoisen laivaston ruokkimiseksi, sek merimiesten pipojen, turkistakkien, villahousujen yms. sotatarvikkeiden ostamiseksi.\n\n[Kuva itvaltalaissotilaasta kttelemss paikallista miest]\n\nRomanian uljaan kansan ht on kuitenkin vistymss Itvalta-Unkarin rauhanturvajoukkojen tuodessa alueelle kauan kaivattua turvaa sek herkullista itvaltalaista ruokaa.\n\n[Kuva pojasta nauttimassa wieninleikkeest]",
				"id": "e66da36bc4189b3e9acfaa50b9a887c7bc0ba31a"
			},
			{
				"time": "fall 1903",
				"from": "Italy",
				"body": "Wieninleike oli taatusti tehty keisarikunnan korkealuokkaisimmasta vasikanlihasta ja tarjoiltu asianmukaisesti lmpimn perunasaalatin, kapristen, sitruunan kera. Huhut idst ovat kertoneet heikkoluokkaisesta venlisest sianlihasta, joka ei tyttisi lnsimaisia hygieniastandardeja. Polttoainepulan takia kypsennys on mys jnyt vajavaiseksi, mink takia olemme saaneet raportteja Trichinella-sukuun kuuluvien matojen aiheuttamista parasiitti-infektioista ja niiden aiheuttamista kuolemista.\n\nVenlisjoukkojen huhutaan mys tarjonneen tt vaarallista sianlihaa maassa oleskeleville turkkilaisjoukoille jniksenlihana.\n\nEnzo Ferrari,\nItalian ulkoministerin tiedottaja",
				"id": "38db15c57033b656faf57828a80a5e7e7738b393"
			},
			{
				"time": "fall 1903",
				"from": "Turkey",
				"body": "Allah soikoon,\n\nmikli nm huhut pitvt paikkaansa, niin tt vaarallista lihaa on tarjoiltu mys vliaikaissulttaani Ramilk Abdul Muhammedille. Voiko Venj vahvistaa, pitvtk Italian syytkset paikkaansa?",
				"id": "e49d2d50f37c56851334ea4f4419f0272e42a240"
			},
			{
				"time": "fall 1903",
				"from": "Russia",
				"body": "Keisarillinen tutkintakomitea on selvittnyt Italian vitteit ja todennut ne perttmiksi.\n\nKomitean mukaan  vite on sula mahdottomuus sill kaikki siat sytiin jo vuonna 1901 sen jlkeen kun Englannin laivasto saartoi Pietarin sataman. Saarto aiheutti akuutin elintarvikepulan koko Ural-vuoriston Euroopan puoleisella alueella.\n\nVuonna 1902 Siperiasta saapui viimein ensimminen hapansilkkalhetys joka on ollut keisarikunnan pasiallinen ravinnonlhde siit asti.\n\nVenjn keisarin puolesta,\nKiril Atrilli\nMaatalousministeri",
				"id": "832a4334c314e4cd4ab906fc1bae0b12a3cc84e6"
			},
			{
				"time": "fall 1903",
				"from": "Austria",
				"body": "Itvalta-Unkari haluaa huomauttaa, ett nkemyksemme mukaan Venjn keisarilliset tutkintakomiteat ovat perinteisesti tuottaneet Tsaarin nkemyksille edullisia tutkimustuloksia tosiasioista juurikaan piittaamatta. \n\nTst huolimatta olemme tll kertaa taipuvaisia uskomaan komiteaa, ainakin silt osin kuin se vitt syttneens hapansilakkaa Romanian kansalaisille. Itvalta-Unkari tuomitseekin ankarasti hapansilakan valmistuksen, kuljetuksen ja viattomien Romanian kansalaisten altistamisen tlle bioaseista kauheimmalle. \n\nJrkyttyneen, \nErnest von Koerber\nItvalta-Unkarin tiedotus- ja sisministeri, kenttmarsalkka, chef cordon bleu",
				"id": "64bc6a69b147ad0b1f2b126de4917da34097c449"
			}
		]
